@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box}.App{display:flex;height:100vh}.main-calculator{display:flex;margin:auto;width:520px;height:757px;border:1px solid #707070;background-color:#393939;border-radius:54px}.lightgrey-calculator{display:flex;flex-direction:column;margin:auto;margin-top:60px;width:479px;height:670px;background-color:#e1e0df;box-shadow:5px 5px 10px #000000;border-radius:2px}#display-shadow{display:flex;margin:auto;margin-top:30px;width:451px;height:124px;background-color:#706e6c;border-radius:13px}#display{margin:auto;width:439px;height:114px;background-color:#c7c7ae;border:0.2 solid #707070;box-shadow:2px 2px 3px #2e2e2e;border-radius:3px}#display h1,#display h2{text-align:right;margin-right:10px}#display h2{font-size:50px}#display h1{font-size:40px}.button{margin:auto;display:grid;grid-template-columns:repeat(4, 100px);grid-template-rows:repeat(5, 80px);justify-content:center;grid-column-gap:15px;-webkit-column-gap:15px;column-gap:15px;grid-row-gap:10px;row-gap:10px;width:465px;height:440px}.buttons{display:flex;justify-content:center;align-items:center;width:100px;height:75px;border-radius:13px;background-color:#FDFAF7;border:1px solid #191919;box-shadow:0px 5px 3px #00000029;font-family:'Rubik', sans-serif;color:#4A4A4A;font-weight:500;text-align:center;font-size:42px;cursor:pointer}#clear,#divide,#multiply,#subtract,#add{background-color:#868686;color:#FDF9F8}#clear{width:217px;grid-column:1 / span 2}#equals{background-color:#FF8067;color:#FDF9F8;height:165px;grid-row:4 / span 2;grid-column:4}#zero{width:217px;grid-column:1 / span 2}

